@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0-beta.2/css/bootstrap.min.css);@import url(https://fonts.googleapis.com/css?family=Titillium+Web:400,900|Encode+Sans+Condensed:300,900&effect=emboss);@import url(https://cdnjs.cloudflare.com/ajax/libs/simple-line-icons/2.4.1/css/simple-line-icons.css);
@charset "UTF-8";
body {
  font-family: 'Titillium Web', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Titillium Web', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
}
.navbar-brand {
  font-weight: bold;
}
header.masthead {
  position: inherit;
  background-color: black;
  padding-top: 8rem;
  padding-bottom: 8rem;
  background: url(/static/img/sky.333c7b4.jpg) no-repeat center center;
  background-repeat: no-repeat;
  -ms-background-size: cover;
  background-size: cover;
}
@media screen and (min-width: 1440px) {
header.masthead {
      background-position: 0px -260px;
}
}
header.masthead .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    opacity: 0.3;
}
header.masthead h1 {
    font-size: 2rem;
}
@media (min-width: 768px) {
header.masthead {
      padding-bottom: 12rem;
}
header.masthead h1 {
        font-size: 3rem;
}
}
.center-content {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.quote {
  font-family: 'Encode Sans Condensed ', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: #868e96 !important;
  font-size: 22px;
  line-height: 1.4rem;
  height: 100%;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: auto;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -webkit-font-kerning: normal;
          font-kerning: normal;
  color: #1A222F;
  position: relative;
  letter-spacing: .03em;
  margin-bottom: .5rem;
}
.quote:before {
    content: "\201C";
    position: absolute;
    left: -.4em;
}
.quote:after {
    content: "\201D";
    position: relative;
    left: .1em;
    margin-right: -1rem;
}
.showcase .showcase-text {
  padding: 3rem;
}
.showcase .showcase-img {
  min-height: 30rem;
  background-size: cover;
}
@media (min-width: 768px) {
.showcase .showcase-text {
    padding: 7rem;
}
}
.features-icons {
  padding-top: 7rem;
  padding-bottom: 7rem;
}
.features-icons .features-icons-item {
    max-width: 20rem;
}
.features-icons .features-icons-item .features-icons-icon {
      height: 7rem;
}
.features-icons .features-icons-item .features-icons-icon i {
        font-size: 4.5rem;
}
.features-icons .features-icons-item:hover .features-icons-icon i {
      font-size: 5rem;
}
.testimonials {
  padding-top: 7rem;
  padding-bottom: 7rem;
}
.testimonials .testimonial-item {
    max-width: 18rem;
}
.testimonials .testimonial-item img {
      max-width: 12rem;
      -webkit-box-shadow: 0px 5px 5px 0px #adb5bd;
              box-shadow: 0px 5px 5px 0px #adb5bd;
}
.call-to-action {
  position: relative;
  font-family: 'Titillium Web', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  background-color: black;
  padding-top: 7rem;
  padding-bottom: 7rem;
}
.call-to-action .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    opacity: 0;
}
.call-to-action:before {
  background-size: cover;
  content: ' ';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: url(/static/img/sky.333c7b4.jpg) no-repeat center center;
  background-repeat: no-repeat;
  background-position: 50% 0px;
  -ms-background-size: cover;
  background-size: cover;
}
footer.footer {
  padding-top: 4rem;
  padding-bottom: 4rem;
}

#ppBody {
  font-size: 11pt;
  width: 100%;
  margin: 0 auto;
  text-align: justify;
}
#ppHeader {
  font-family: verdana;
  font-size: 21pt;
  width: 100%;
  margin: 0 auto;
}
.ppConsistencies {
  display: none;
}
